body {
  margin: 0;
  background-color: #444;
}

.nav-bar {
  background-color: rgba(0, 0, 0, 0.3);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  border-bottom-style: solid;
  border-bottom-color: rgba(255,255,255, 0.4);
  box-shadow: 0px 2px 20px -4px;
  border-width: thin;
  height: 30px;
  padding-top: 6px;
}

.nav-brand {
  border: none;
  background-color: transparent;
  font-size: 12pt;
  color: rgba(255, 255, 255, 1);
  text-shadow: 0.4px 0.4px rgba(255, 255, 255, 0.8), -1px -1px rgba(0, 0, 0, 1);
  outline: none;
  font-family: 'Fredoka One', cursive;
  padding-left: 20px;
}

.nav-link {
  float: right;
  padding-right: 20px;
  border: none;
  background-color: transparent;
  font-size: 12pt;
  color: rgba(255, 255, 255, 0.8);
  outline: none;
  font-family: "Roboto";
  font-weight: bold;
}


.main-canvas {
  background-color: #FFF;
  position: absolute;
  top: 48%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: rgb(0, 0, 0) 0px 4px 10px;
  border: none;
  z-index: -2;
  border-radius: 10px;
}

.iframe-shield {
  position: absolute;
  top: 48%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: -1;
}


.menu-bar {
  background-color: rgba(0, 0, 0, 0.1);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  border-style: solid;
  border-color: rgba(255,255,255, 0.2);
  box-shadow: 0px 8px 20px 4px;
  border-width: thin;
  position: absolute;
  top: 94%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 12px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  height: 60px;
}

.action-btn {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 14px 40px;
}


.menu-btn {
  display: inline-block;
  position: absolute;
  top: 94vh;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  border: none;
  background-color: transparent;
  outline: none;
}

.menu-arrow:hover {
  fill: rgba(50, 50, 50, 0.6);
}



.code-box-container {
  background-color: #162228;
  border-style: solid;
  border-color: rgba(255,255,255, 0.4);
  box-shadow: 0px 8px 20px 4px;
  border-width: thin;
  min-width: 350.4px;
  min-height: 202px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 16px;
  /* overflow: hidden; */
}

.code-box {
  height: 100%;
  padding-bottom: 50px;
  box-sizing: border-box;
  overflow: scroll;
}

.code-box::-webkit-scrollbar {
  display: none;
}

.CodeMirror {
  position: relative;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  height: 100% !important;
  width: 100% !important;
}

.react-codemirror2::-webkit-scrollbar {
  display: none;
}

.CodeMirror-hscrollbar {
  display: none !important;
}

.CodeMirror-scrollbar-filler {
  display: none !important;
}


.react-codemirror2 {
  height: 100% !important;
  width: 100% !important;
}

.code-btn {
  border: none;
  border-radius: 16px 16px 0px 0px;
  outline: none;
  color: white;
  background-color: #162228;
  padding: 8px 40px;
}

.code-btn:hover {
  background-color: #263238 !important;
}

